import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import Container from "@material-ui/core/Container";
import SimpleBreadcrumbs from "../../layout/SimpleBreadcrumbs";
import Markdown from "markdown-to-jsx";
import Highlighter from "../../components/PrismSyntaxHighlighter";
import Footer from "../../components/Footer";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  root: {
    width: 300,
  },
  container: {
    display: "flex",
    flexDirection: "column",
    [theme.breakpoints.between("xs", "md")]: {
      minHeight: "calc(100vh - 131px)",
    },
    [theme.breakpoints.up("md")]: {
      minHeight: "calc(100vh - 225px)",
    },
    paddingBottom: theme.spacing(8),
  },
}));

export default function Post(props) {
  const classes = useStyles();
  const [error, setError] = useState(false);
  const { category, post, name, file } = useParams();
  const [content, setContent] = useState("");

  const getWidth = (filteredInt, defaultWidth = "80%") => {
    const data = filteredInt.match(/(\d+)/);
    if (data) {
      return data[0] + "%";
    } else {
      return defaultWidth;
    }
  };
  const getDefaultImageName = (path, ext) => {
    return path + `/post-header1920x200.${ext}`;
  };
  const loadImage = () => {
    //console.log("---===URL===---");
    //console.log(`/static/md/${category}/${post}/post-header-1.jpg`);
    //console.log("---===Category===---");
    //console.log(`/static/md/${category}`);
    return (
      <img
        src={`/static/md/${category}/${post}/post-header-1.jpg`}
        alt="header"
        onError={(e) => {
          if (!error) {
            setError(true);
            e.target.src = getDefaultImageName(`/static/md/${category}`, "jpg");
          } else {
            import("../../assets/img/image_not_found.jpg");
            e.target.src = "/static/img/image_not_found.jpg";
          }
        }}
        style={{
          width: "100%",
          height: "150px",
          backgroundSize: "cover",
          objectFit: "cover",
        }}
      />
    );
  };
  useState(() => {
    // console.log("category: " + category);
    // console.log("post: " + post);
    // console.log("name: " + name);
    // console.log("file: " + file);
    window.scrollTo(0, 0);
    fetch(`/static/md/${category}/${post}/${file}.md`)
      .then((e) => e.blob())
      .then((file) => {
        if (file && file.type && file.type.includes("text/markdown")) {
          return file.text();
        }
        return "";
      })
      .then((text) => setContent(text));
  }, []);
  return (
    <React.Fragment>
      {loadImage()}
      <Container maxWidth="md" className={classes.container}>
        <SimpleBreadcrumbs
          links={[
            { title: "–ì–ª–∞–≤–Ω–∞—è", link: "/", category: "" },
            {
              title: category,
              link: `/category/${category}`,
              category: category,
            },
          ]}
          current={name}
        />
        <main>
          <Markdown
            children={content}
            options={{
              overrides: {
                code: {
                  component: Highlighter,
                },
                img: {
                  component: function (e) {
                    // alt: "action_reducer_store"
                    // className: undefined
                    // src: "/assets/md/react/post5/action_reducer_store_lite.png"
                    // title: undefined
                    return (
                      <img
                        alt={e.alt}
                        className={e.className}
                        src={e.src}
                        title={e.title}
                        style={{
                          width: getWidth(e.alt),
                          height: "auto",
                          display: "block",
                          margin: "0 auto",
                        }}
                      />
                    );
                  },
                },
              },
            }}
          />
        </main>
      </Container>
      <Footer />
    </React.Fragment>
  );
}
